/* Desktop styles */
nav {
    height: 5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #282c34; 
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 1000;
}

nav img {
    margin-left: 25px;
    width: 80px;
    filter: invert(1); 
}

nav ul {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
}

nav ul li {
    margin: 0 25px; 
}

nav ul li a {
    text-decoration: none;
    color: white; 
    font-weight: 300;
    font-size: 1.1rem; 
    transition: color 0.1s ease-in-out;
}

nav ul li a:hover {
    text-decoration: underline;
}

/* Tablet styles */
@media only screen and (max-width: 1024px) {
    nav img {
        width: 60px;
    }

    nav ul li {
        margin: 0 15px;
    }
}

/* Mobile styles */
@media only screen and (max-width: 600px) {
    nav {
        height: auto;
        align-items: flex-start;
    }

    nav img {
        margin: 15px 0 10px 15px;
        width: 60px;
    }

    nav ul {
        align-items: flex-start;
        margin-top: 10px;
    }

    nav ul li {
        margin: 10px 10px;
    }
}
