
.container-3 {
  display: flex;
  justify-content: space-between;
  margin-inline: 10rem;
}

.form {
  flex: 1;
  padding: 20px;
  background-color: #f7f7f7;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

form {
  display: flex;
  flex-direction: column;
}

label {
  margin-bottom: 10px;
}

input {
  width: 100%;
  padding: 8px;
  margin-top: 4px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.buttonquery {
  background-color: red;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.1s, transform 0.1s;
}

.buttonquery:hover {
  background-color: #ff0000;
  transform: scale(1.1);
}

.container {
  flex: 1;
  margin-left: 2rem;
}

.container h1 {
  font-size: 24px;
  margin-bottom: 10px;
}

.container p {
  font-size: 16px;
  line-height: 1.5;
  color: #555;
}

.password-input-container{
  text-align: right;
}

/* Media Query for smaller screens */
@media (max-width: 768px) {
  .container-3 {
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin-inline: 2rem;
  }

  .form {
    margin-bottom: 2rem;
  }

  .container {
    margin-left: 0;
  }
}



span{
  color: red;
  font-size: x-small;
}

