.container-2 {
  margin-inline: 10rem;
}

.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 80px 0;
}

.left-text {
  max-width: 500px;
}

.left-text h1 {
  font-size: 2.5em;
  margin-bottom: 20px;
}

.left-text p {
  font-size: 1.1em;
  line-height: 1.5;
  color: #666;
  margin-bottom: 30px;
}

.buttons {
  margin-top: 20px;
}

.button,
.category {
  padding: 10px 20px;
  margin-right: 15px;
  font-size: 1em;
  cursor: pointer;
  background-color: #ff0000;
  color: #fff;
  border: none;
  border-radius: 5px;
  transition: transform 0.1s ease-in-out;
}

.button:hover,
.category:hover {
  transform: scale(1.1);
}

.ama-flip {
  margin-top: 20px;
}

.ama-flip p {
  font-size: 1em;
  margin-bottom: 10px;
}

.ama-flip img {
  cursor: pointer;
  max-width: 100px;
  height: auto;
}

.right-image {
  margin-top: 100px;
  text-align: right; 
}

.right-image img {
  max-width: 80%; 
  max-height: 80%; 
  object-fit: contain; 
  border-radius: 8px;
  overflow: hidden;
  display: block; 
  margin-left: auto; 
  margin-right: 0; 
  transition: transform 0.3s ease-in-out;
}


.right-image:hover img {
  transform: scale(1.1);
  cursor: pointer;
}

/* Media Queries for Responsive Design */

/* Tablets */
@media only screen and (max-width: 1024px) {
  .container-2 {
      margin-inline: 5rem;
  }

  .hero {
      flex-direction: column;
      text-align: center;
  }

  .left-text {
      max-width: 100%;
  }

  .right-image {
      margin-top: 50px;
  }
}

/* Mobile */
@media only screen and (max-width: 600px) {
  .container-2 {
      margin-inline: 2rem;
  }

  .hero {
      padding: 60px 0;
  }

  .left-text h1 {
      font-size: 2rem;
      margin-bottom: 15px;
  }

  .left-text p {
      font-size: 1em;
      margin-bottom: 20px;
  }

  .buttons {
      margin-top: 15px;
  }

  .button,
  .category {
      padding: 8px 15px;
      margin-right: 10px;
      font-size: 0.9em;
  }

  .ama-flip {
      margin-top: 15px;
  }

  .ama-flip p {
      font-size: 0.9em;
      margin-bottom: 8px;
  }

  .ama-flip img {
      max-width: 80px;
  }

  .right-image {
      margin-top: 50px;
  }
}
